/* global styles */
@import '../../App.scss';

.downloadResumeButton {
    //TODO: update download resume button to look nicer
    background-image: $light-blue-solid;
    color: $text-color-white;
    font-size: 16px;
    border-radius: 10%;
    &:hover {
        box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 25px 0 rgba(0,0,0,0.19);
        background-image: linear-gradient(#051726, #051726);
        cursor: pointer;
    }
}
